/* eslint-disable
  no-param-reassign,
  no-plusplus,
  no-continue,
  no-underscore-dangle,
*/

// Constants
const gravity = 9.80665; // px/s
const forceMultiplier = 1.5;
const sizeMultiplier = 2;
const minMass = 10;
const maxMass = 50;
const massMultiplier = 1;
const eventThrottle = 16;

// Static
const raf = window.requestAnimationFrame;

export default class Physics {
  constructor(canvas) {
    this.canvas = canvas;
    this.initCanvas();

    this.particles = [];

    this._onMouseMove = this.onMouseMove.bind(this);
    window.addEventListener('mousemove', this._onMouseMove);
    this._onTouchMove = this.onTouchMove.bind(this);
    window.addEventListener('touchmove', this._onTouchMove);

    this.rendering = false;

    this._tick = this.tick.bind(this);
    this._tick();
  }

  destroy() {
    this.isDestroyed = true;

    this.particles = null;
    this.canvas = null;
    this.ctx = null;
    window.removeEventListener('mousemove', this._onMouseMove);
    this._onMouseMove = null;
    window.removeEventListener('touchmove', this._onTouchMove);
    this._onTouchMove = null;
    window.removeEventListener('resize', this.updateCanvasSize);
  }

  initCanvas() {
    this.ctx = this.canvas.getContext('2d');
    this.dpr = window.devicePixelRatio;
    this.vw = window.innerWidth;
    this.vh = window.innerHeight;
    this.setCanvasSize(this.vw, this.vh);

    this.updateCanvasSize = () => {
      this.dpr = window.devicePixelRatio;
      this.vw = window.innerWidth;
      this.vh = window.innerHeight;
      this.setCanvasSize(this.vw, this.vh);
    };

    window.addEventListener('resize', this.updateCanvasSize);
  }

  setCanvasSize(w, h) {
    this.canvas.width = w * this.dpr;
    this.canvas.height = h * this.dpr;
  }

  clearCanvas() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  createParticle = (mass, x, y, force) => {
    this.particles.push({
      mass,
      x,
      y,
      prevX: x,
      prevY: y,
      gravOffset: 0,
      created: performance.now(),
      color: Physics.randomColor(),
      force,
    });
    if (!this.rendering) {
      this.rendering = true;
      this._tick();
    }
  };

  removeParticle(index) {
    this.particles.splice(index, 1);
  }

  tick(now) {
    if (this.isDestroyed) return;

    const {
      prevTick,
      particles,
      ctx,
      vh,
      dpr,
    } = this;

    const deltaTime = prevTick !== undefined ? now - prevTick : 0;
    this.prevTick = now;

    this.clearCanvas();

    for (let i = 0, l = particles.length; i < l; i++) {
      const particle = particles[i];

      const lifetime = now - particle.created;
      particle.velocity = Physics.getVelocity(
        particle.mass * massMultiplier,
        lifetime > 0 ? lifetime / 1000 : 0,
      );

      particle.gravOffset += particle.velocity * (deltaTime > 0 ? deltaTime / 1000 : 0);

      Physics.applyForce(particle, now);

      const manipulationX = particle.forceOffsetX;
      const manipulationY = particle.gravOffset + particle.forceOffsetY;
      const x = particle.x + manipulationX;
      const y = particle.y + manipulationY;

      // const deltaX = x - particle.prevX;
      const deltaY = y - particle.prevY;
      particle.prevX = x;
      particle.prevY = y;

      const size = Math.sqrt(particle.mass * 2) * sizeMultiplier;
      particle.color = Physics.getDirectionColor(deltaY);

      // Remove if off-screen
      if (y - (size * 2) > vh * dpr) {
        this.removeParticle(i);
        i--;
        l--;
        continue;
      }

      // Redraw
      ctx.beginPath();
      ctx.moveTo(x, y);
      ctx.arc(
        x,
        y,
        size,
        size,
        0,
        Math.PI * 2,
      );
      ctx.fillStyle = particle.color;
      ctx.fill();
      ctx.closePath();
    }
    if (particles.length === 0) {
      this.rendering = false;
    } else {
      raf(this._tick);
    }
  }

  onMouseMove({ pageX, pageY }) {
    this.createParticleAtPoint({ x: pageX, y: pageY });
  }

  onTouchMove({ changedTouches }) {
    if (changedTouches.length === 0) return;
    const { pageX, pageY } = changedTouches[0];
    this.createParticleAtPoint({ x: pageX, y: pageY });
  }

  createParticleAtPoint({ x, y }) {
    const {
      prevTime,
      lastPass,
      prevX,
      prevY,
      dpr,
    } = this;

    const now = performance.now();
    const deltaTime = prevTime === undefined ? 0 : now - prevTime;
    this.prevTime = now;

    if (lastPass !== undefined && now - lastPass < eventThrottle) return;
    this.lastPass = now;

    const deltaX = prevX === undefined ? 0 : x - prevX;
    const deltaY = prevY === undefined ? 0 : y - prevY;
    const absDelta = Math.abs(deltaX) + Math.abs(deltaY);
    this.prevX = x;
    this.prevY = y;

    if (!this.hasInitialized) {
      // Make sure we have prev*
      this.hasInitialized = true;
      return;
    }

    // Create particle at point
    this.createParticle(
      Math.max(Math.min(Math.abs(absDelta), maxMass), minMass),
      x * dpr,
      y * dpr,
      Physics.getVector(deltaX, deltaY, deltaTime),
    );
  }

  static getVelocity(mass, sec) {
    return mass * (gravity * sec);
  }

  static getVector(deltaX, deltaY, deltaTime) {
    const rad = Math.atan2(deltaY, deltaX);
    const circDelta = Math.sqrt((deltaX ** 2) + (deltaY ** 2));
    const absDelta = Math.abs(circDelta);
    const magnitude = (absDelta / deltaTime) * forceMultiplier;
    return [
      rad,
      magnitude,
    ];
  }

  static applyForce(particle, now) {
    const [angle, magnitude] = particle.force;
    const deltaTime = Math.max(0, now - particle.created);
    const currentMagnitude = Math.max(0, magnitude * Math.sqrt(deltaTime));
    particle.forceOffsetX = Math.cos(angle) * currentMagnitude;
    particle.forceOffsetY = Math.sin(angle) * currentMagnitude;
  }

  static randomColor = () => `hsl(
    ${Math.floor(Math.random() * 360)},
    50%,
    ${Math.random() * 50}%
  )`;

  static getDirectionColor = deltaY => `hsl(
    ${
      document.documentElement.classList.contains('light')
        ? Math.min(360, Math.max(225, (deltaY / 10) * 360))
        : Math.min(360, Math.max(225, (deltaY / -100) * 360))
    },
    90%,
    80%
  )`;
}
